          </h1>
        </div>

        <div class="right">
          <template v-if="pc">
              <div class="prompt">
                $<span>&#x258c;</span>
              </div>
          </template>

          <div class="prof">
            <strong>Yasuhiro Yamada</strong><br>
            Shellgei lover, Cure Engineer, Unkontributors, Egison maintener<br>
            <img src="img/ic_email.svg" alt="email"> yamada[at]gr3.ie
