        </div>

        <template v-if="pc">
          <div class="arrow">
            <div class="arrowWrap">
              <span></span>
              <span></span>
            </div>
          </div>
        </template>

        <Ojichat :containStyle="containStyle" :pc="pc"></Ojichat>

      </div>
    </div>

    <div id="work">
      <div class="wrap">
        <h2 class="workTitle">SNS</h2>

        <ul class="sns">
          <li><a href="https://github.com/greymd" target="_blank"><img src="img/ic_github.svg" alt="GitHub"></a></li>
          <li><a href="https://bsky.app/profile/gr3.ie" target="_blank"><img src="img/ic_bluesky.svg" alt="Twitter"></a></li>
          <li><a href="https://misskey.io/@grethlen" target="_blank"><img src="img/ic_misskey.svg" alt="Twitter"></a></li>
          <li><a href="https://x.com/grethlen" target="_blank"><img src="img/ic_x.svg" alt="Twitter"></a></li>
        </ul>

        <h2 class="workTitle">Community</h2>
        <ul class="communityList">
          <li><a href="https://www.facebook.com/groups/141271259271220" target="_blank">Shellgei</a></li>
          <li><a href="https://github.com/unkontributors" target="_blank">Unkontributors</a></li>
