        </ul>
        <h2 class="workTitle">Softwares</h2>
        <ul class="softwaresList">
          <li v-for="oss in osses" v-bind:key="oss.title">
            <a v-bind:href="oss.url" target="_blank">
              <div class="img"><img :src="oss.icon" /></div>
              <div class="title">{{oss.title}}</div>
